import React from 'react';

import NavBar from '../Common/NavBar';
import buttons from './text/buttons';
import '../../styles/content.css';

class About extends React.Component {
    state = {
        currentView: 0,
        prevView: 0,
        count: 0,
    }

    onButtonClick = (index) => {
        if (index !== this.state.currentView) {
            this.setState({currentView: index });
        }
    }

    render() {
        return (
            <div className="content solid-border">
                <div className="hello">
                    <h1>Cześć jestem Magda!</h1>
                    <p>Zapraszam do zapoznania się z moimi projektami.</p>
                </div>
                <div className="nav-bar-cont">
                    <NavBar buttons={buttons} onButtonClick={this.onButtonClick} />
                </div>
                <div className="box">
                    {buttons[this.state.currentView].content} 
                </div>
            </div>
        )
    }
}

export default About;